import React from 'react';
import Head from 'next/head';

import fetch from 'isomorphic-unfetch';

import NavbarUst from '../components/NavbarUst';
import ArticleCards from '../components/ArticleCards';

class Articles extends React.Component {
  constructor(props){
    super(props);

    this.state= {
      page: ''
    }
  }

  static async getInitialProps(){
    const res = await fetch('http://localhost:3001/articles?perPage=2')
    let data = await res.json();
    data = data['docs'];
    return {data: data.reverse()}
  }

  setPage = (page) => {
    console.log(page)
  }

  render() {
    return (
    <React.Fragment>
      <Head>
        <title>Berk Elmas | Makaleler</title>
        <link rel='stylesheet' href='static/css/reactstrap.min.css' />
        <link rel='stylesheet' href='static/css/style.css' />
      </Head>
      <NavbarUst/>
      <ArticleCards page={this.setPage} data={this.props.data} />
      <br/>
      <hr/>
    </React.Fragment>
    )
  }
}

export default Articles;
